html, body {
  margin: 0;
  padding: 0;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  position: relative;
  width: 100%;
  min-height: 100%;
}
.App {
  width: 100%;
  min-height: 100%;
  background: rgb(240, 245, 251);
  position: absolute;
  top: 0;
  left: 0;
}

.ui.segment.Welcome-segment {
  margin-top: 3em;
}

.Welcome-input-error {
  display: block;
  text-align: left;
  color: #d6322f;
  width: 248px;
  margin: auto;
}

.App-OpenUnreadButton {
  width: 100%;
  text-align: right;
}
